<template>
  <div class="navbar">
    <van-nav-bar
      :title="text"
      left-text="返回"
      right-text="分享"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />

    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
      @select="onSelect"
    />

    <van-overlay :show="show" @click="show = false">
      <div class="overlay_box">
        <div class="overlay_con">
          <div>
            <div class="overlay_wz">扫描二维码进入微信客服</div>
            <div class="overlay_wz">进行预约和咨询</div>
          </div>
          <div>
            <img
              style="width: 10rem; height: 10rem"
              :src="imgUrlarr.src"
              alt=""
            />
          </div>
        </div>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  name: "navbar",
  data() {
    return {
      text: "",
      imgUrlarr: {
        src: 'https://tvax2.sinaimg.cn/large/006D2oSpgy1h5d8zxpkm0j30ap0apwgp.jpg',
      },
      showShare: false,
      show: false,
      options: [
        { name: "微博", icon: "weibo" },
        { name: "QQ", icon: "qq" },
        { name: "复制链接", icon: "link" },
        { name: "客服二维码", icon: "qrcode" },
      ],
    };
  },
  methods: {
    get_text() {
      this.text = this.$route.query.text;
    },
    onClickLeft() {
      let str = this.$route.path;
      // let str2 = this.$route.query.id;
      let str4 = window.location.href;
      // let id = this.id;
      if (str.indexOf("information") == 1) {
        // console.log(str.indexOf('rubik_'));
        // this.$router.push("/gogl?id=" + id + "&text=客户故事");
        this.$router.push("/index");
      }
      if (str.indexOf("story") == 1) {
        // console.log(str.indexOf('rubik_'));
        // this.$router.push("/gogl?id=" + id + "&text=客户故事");
        this.$router.push("/index");
      }
      if (str.indexOf("addus") == 1) {
        // console.log(str.indexOf('rubik_'));
        // this.$router.push("/gogl?id=" + id + "&text=客户故事");
        this.$router.push("/index");
      }
      if (str.indexOf("dy") == 1) {
        // console.log(str.indexOf('rubik_'));
        // this.$router.push("/gogl?id=" + id + "&text=客户故事");
        this.$router.push("/index");
      }
      if (str.indexOf("wz_") == 1) {
        // console.log(str.indexOf('rubik_'));
        // this.$router.push("/gogl?id=" + id + "&text=客户故事");
        this.$router.push("/information");
      }
      if (str.indexOf("sp") == 1) {
        // console.log(str.indexOf('rubik_'));
        // this.$router.push("/gogl?id=" + id + "&text=客户故事");
        this.$router.push("/index");
      }
    },
    onClickRight() {
      this.showShare = true;
    },
    // onSelect(option) {
    //   Toast(option.name);
    //   this.showShare = false;
    // },

    copy(url) {
      let inp = document.createElement("input");
      inp.value = url;
      document.body.appendChild(inp);
      inp.select(); // 选择对象;
      // console.log(inp.value, '复制到的内容')
      document.execCommand("Copy"); // 执行浏览器复制命令
      this.$message.success("复制成功");
      inp.remove();
    },
    onSelect(option) {
      // Toast("正在跳转" + option.name);
      this.showShare = false;

      //二维码
      if (option.name == "客服二维码") {
        Toast("已经生成" + option.name);
        this.show = true;
      }
      // 复制链接
      if (option.name == "复制链接") {
        Toast("快去微信分享给好友吧~");
        this.show2 = true;
        let url = window.location.href;
        console.log(window.location.href);
        this.copy(url);
        // window.location.href = window.location.href;
        // console.log(window.location.href);
      }
      // QQ
      if (option.name == "QQ") {
        Toast("正在前往QQ空间分享");
        window.location.href =
          "http://connect.qq.com/widget/shareqq/index.html?url=" +
          this.Url +
          "&sharesource=qzone&title=装修找我们&pics=你的分享图片地址&summary=你的分享描述&desc=你的分享简述";
      }

      //

      // 新浪微博
      if (option.name == "微博") {
        Toast("正在前往微博分享");
        window.location.href =
          "http://service.weibo.com/share/share.php?url=" +
          this.Url +
          "&sharesource=weibo&title=装修找我们&pic=你的分享图片&appkey=你的key，需要在新浪微博开放平台中申请";
      }
      // this.$router.go(-1);
    },
  },
  created() {
    this.get_text();
  },
};
</script>

<style  scoped>
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
}
.van-nav-bar {
  background-color: #342c2a;
}
.navbar >>> .van-nav-bar__text {
  color: white;
}
.navbar >>> .van-nav-bar .van-icon {
  color: white;
}
.navbar >>> .van-nav-bar__title,
.van-ellipsis {
  color: white;
}
.navbar >>> .overlay_box {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70%;
}
.navbar >>> .overlay_con {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
 .navbar >>> .overlay_wz {
  text-align: center;
  color: white;
}
</style>